<template>
    <div class="titleProducts"><span>Các loại chè mới nhất</span></div>
    <div class="productsContainer">
        <div class="productsShow" v-for="(item, index) in content" v-bind:key="index">
            <img v-bind:src="item.src" alt="product image">
            <div class="nameProduct">{{ item.title }}</div>
            <div class="priceProduct">
                {{ price(item.price) }}
            </div>
            <button class="buyButton">Mua ngay</button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'products-home',
    props: {
        content: {
            type: Array,
            default: null
        },
    },
    data() {
        return {

        }
    },
    methods: {
        price(price) {
            const formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'VND',
                minimumFractionDigits: 0
            });
            return formatter.format(price);
        }
    }
}
</script>
<style scoped>
.productsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 20px;
    background-color: #f9f9f9;
}

.titleProducts {
    width: 100%;
    font-size: 36px;
    font-family: 'Arial', sans-serif;
    font-weight: bold;
    text-align: center;
    color: #333;
    margin: 20px 0px;
}

.productsShow {
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    padding: 20px;
    transition: transform 0.3s ease-in-out;
}

.productsShow:hover {
    transform: translateY(-10px);
}

.productsShow img {
    width: 100%;
    height: auto;
    max-width: 200px;
    margin-bottom: 15px;
    transition: transform 0.3s ease-in-out;
}

.productsShow:hover img {
    transform: scale(1.05);
}

.nameProduct {
    font-size: 24px;
    font-family: 'Arial', sans-serif;
    color: #333;
    margin-bottom: 10px;
}

.priceProduct {
    font-size: 20px;
    color: #e63946;
    margin-bottom: 15px;
    font-family: 'Arial', sans-serif;
    font-weight: bold;
}

.buyButton {
    background-color: #e63946;
